@import url(https://fonts.googleapis.com/css2?family=Merriweather&family=Merriweather+Sans:wght@300&display=swap);
.milestone {
    width: 100%;
    margin-bottom: 30px;
    background: rgba(255, 255, 255, .3);
    padding: 30px;
    border-radius: 5px;
}

.milestone-date {
    font-weight: 900;
    font-size: 26px;
    color: var(--complementary-text-color);
}
:root {
    --primary-color: #000;
    --primary-text-color: #fff;
    --complementary-text-color: #ffbd39;
    --font-size: 13px;
}

.my-button {
    outline: none;
    border-radius: 1em;
}

.my-container {
    background-color: black;
    align-items: center;
    height: 100% !important;
    width: 100% !important;
    color: white;
}

.heading {
    font-size: 46px;
    color: white;
    font-weight: bold;

}

.section-intro {
    transition: -webkit-transform 3s;
    transition: transform 3s;
    transition: transform 3s, -webkit-transform 3s;
}
/*General styling for structure*/
body {
    margin: 0;
    font-family: "Merriweather Sans", sans-serif;
}

.container {
    max-width: 1280px;
    width: 95%;
    margin: 0 auto;
}

header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-family: "Merriweather", serif;
    height: 100vh;
}

header h2 {
    font-weight: 400;
}

.scroll-container {
    height: 100vh;
    min-height: 450px;
    padding: 2rem 1rem;
    display: flex;
    align-items: center;
    box-sizing: border-box;
}

.scroll-caption {
    margin: 1rem;
}

/**Styling scrollable elements*/

.js-scroll {
    opacity: 0;
    transition: opacity 500ms;
}

.js-scroll.scrolled {
    opacity: 1;
}

.scrolled.fade-in {
    -webkit-animation: fade-in 1s ease-in-out both;
            animation: fade-in 1s ease-in-out both;
}

.scrolled.fade-in-bottom {
    -webkit-animation: fade-in-bottom 1s ease-in-out both;
            animation: fade-in-bottom 1s ease-in-out both;
}

.scrolled.slide-left {
    -webkit-animation: slide-in-left 1s ease-in-out both;
            animation: slide-in-left 1s ease-in-out both;
}

.scrolled.slide-right {
    -webkit-animation: slide-in-right 1s ease-in-out both;
            animation: slide-in-right 1s ease-in-out both;
}

